import React from "react";
import SearchBar from './SearchBar'
import SectionBar from './SectionBar'
import { CirclePicker } from 'react-color';
import Home from "./Map_Loading";
import OutletList from "./OutletList";
// import Clip from "./Clipboard";
// import SampleComponent from "./json2xl";
import DownloadExcel from "./json2xl";


const selected=[]
const out=[]
for(let i=0;i<139;i++){
    selected[i]={id:i, label: null , color:null,section: null}
    out[i]={    }
    }

class App extends React.Component{
    state  = { id:0, color: "#ffa", label:null, value:[], copied:false, section:null};
    onSelectedOutlet = (out) => {
        this.setState({id:out.value, label:out.label})        
        // if(selected[this.state.id].includes(out))
        //           {const index = selected[this.state.id  ].indexOf(out);
        //             if (index > -1) { // only splice array when item is found
        //                 selected[this.state.id].splice(index, 1); // 2nd parameter means remove one item only
        //             }}
        //             else {
        //                 selected[this.state.id].push(out)
        //             }
        // selected.push({id:this.state.id, color:this.state.color})
    };
    onSelectedSection = (out) => {
        this.setState({section:out.value})
    };
    onOutletChange = (event) => {
        this.setState({value: event})
    };

    onSelectedColor = (col) => {
        this.setState({color: col.hex})
    };

    render(){
        const { id, color, label, value, section} = this.state;
        // console.log(id,color,label)
        selected[id].id = id
        selected[id].label = label
        selected[id].section = section
        
        if(color === undefined){
            selected[id].color = "#ffa" }
            else{
                selected[id].color = color
            }

        

        // console.log(selected)
        // console.log(value)
        return( 
            <div className="ui two column grid ">
              <div className="ui twelve wide column">
                <Home selectedColor ={color} selectedOut={id} onOutletChange={this.onOutletChange} selectedSection = {section}/>
                </div>
                <div className="ui four wide column">
                    <div className="row">
                    <SearchBar onFormSubmit={this.onSelectedOutlet}/></div>
        <div className="row"><div className="ui segment"><CirclePicker onChangeComplete={this.onSelectedColor } /></div></div>
        {/* colors={["#9E9E9E","#E91E63","#00E676","#9C27B0","#F44336","#FF9100","#607d8b,"#795548
"
"]} */}
    <div className="row">
        <SectionBar onFormSubmit={this.onSelectedSection}/></div>
        <div className="row"><OutletList outlets={selected} numbers={value}/>
        {/* <Clip value={value}/>  */}
        <DownloadExcel data={value}/>    
        </div>
        </div>
        </div> );
    }
}

export default App;