import React from "react";
import { Circle } from "./Shapes";

const OutletTile = (props)=>{
    // console.log(props)
    // console.log(props.outlet)
    const number = props.outlet.id
    // console.log(props.numbers.value[number].length)
    var numberOut = 0
    if(props.numbers.value !== undefined){
        numberOut = props.numbers.value[number].length
    }
    // else
    // { numberOut = 0}
    
    // const name = "ui "+ String(props.outlet.color) + " horizontal label"
    // console.log(name)
    return (
        
    <div className="item">
        <div className="ui two column grid">   
    <div className="column">{props.outlet.label}</div>
    <div className="column" ><Circle r={10} fill={{color:props.outlet.color}} stroke={{color:'#E65243'}} strokeWidth={3} />
    </div></div>Selected Circles : {numberOut}</div> )
};

export default OutletTile;