class DownloadExcel extends React.Component {
  componentDidUpdate(prevProps){
    if(this.props !== prevProps){
        if(this.props.data.value !== undefined){
        for(var i=0;i<this.props.data.value.length;i++){
                // console.log(this.props.data.value[i])
                var prop_arr = []
                var new_arr = []
                const original_keys = Object.keys(outlets_original[i])
                for(var j=0;j<this.props.data.value[i].length;j++){
                    // console.log(this.props.data.value[i][j])
                    const out_num = this.props.data.value[i][j]
                    const out_num_str = out_num.toString()
                    
                    // console.log(keys)
                    // const num = j.toString()
                    // const out_num = this.props.data.value[i][j]
                    const out_num_lat = out_num_str + '_lat'
                    outlets[i][out_num_lat]= outlets[out_num]['lat']
                    const out_num_lng = out_num_str + '_lng' 
                    outlets[i][out_num_lng]=outlets[out_num]['lng']
                    prop_arr.push(out_num_lat,out_num_lng)
                }
                // console.log('original keys',original_keys)
                new_arr = prop_arr.concat(original_keys)
                // console.log(new_arr)
                outlets[i]= _.pick(outlets[i],new_arr)
                // console.log(prop_arr)

            }  
    }
}
  
    
  }
  
  render() {
    // console.log(outlets)
    return (
      <JsonToExcel
        title="Download as Excel Sheet"
        data={outlets}
        // data={[{ test: "test" }]}
        fileName="Grid Coordinates S3 Arena"
        btnClassName="custom-classname"
      />
    );
  }
}

export default DownloadExcel;