import React from "react";
import OutletTile from "./OutletTile";

const OutletList = ({outlets,numbers}) => {
    const renderedOutlets = outlets.map((outlet)=>{
        // console.log(outlet,numbers)
        if(outlet.label !== null){
        return <OutletTile outlet={outlet} numbers={numbers}/>;}
        return <div></div>;
    });

    return <div className="ui divided selection list">{renderedOutlets}</div>;
}

export default OutletList;
