import React from "react";
import Select from 'react-select';

const outlets = require('./gridSource/All Outlets in S3 Arena_underGrid_new.json')


class SearchBar extends React.Component{
    state = { isOpen: false, value: 0, label: null, event: 0 };
    toggleOpen = () => {
        this.setState((state) => ({ isOpen: !state.isOpen }));
      };
    onSelectChange = (event) => {
        this.toggleOpen();
        this.setState({ value: event.value, label:event.label, event: event });
      };

    onFormSubmit = (event) => {
        event.preventDefault();
        this.props.onFormSubmit({value: this.state.value, label: this.state.label});
    }

    render(){
        
        const {event} = this.state;
        // console.log(value, label, event)
        return(<form className="ui segment" onSubmit={this.onFormSubmit}>
            <Select options={outlets} onChange={this.onSelectChange} value={event} placeholder="Select an Outlet">
            </Select>
            </form>);
    }
}

export default SearchBar;