import React from "react";
import {
  GoogleMap,
  Circle,
  Marker
} from "@react-google-maps/api";
import './style.css'
import { Fragment } from "react";
var select = []
const outlets = require('./gridSource/Grid_Kerala_Hex_ids_Arena_new_5km.json');
const outlets_sectionwise = require('./gridSource/Grid_Kerala_Hex_ids_Arena_new_5km_overlay.json')
var outlet_center=0;
for(let i=0;i<139;i++){
select[i]=[]
}

outlets[0].circle = {
  radius: 8000,
   options: {
     strokeColor: "#ffff00",
     clickable: false,
     visible: true
   }
   };

for( let i = 1; i< 139; i++){
    outlets[i].circle = {
     radius: 8000,
      options: {
        strokeColor: "#ff0000",
        clickable: false,
        visible: true
      }
      };
    };

for( let i = 139; i< outlets.length ; i++){
  outlets[i].arr = []
    outlets[i].circle = {
      radius: 5000,
      options: {
        strokeColor: "#000"
      },
      toggle: 0
      };
    };

  
  for( let i = 0; i< 180 ; i++){
    outlets_sectionwise[i].section= 0
      outlets_sectionwise[i].circle = {
       radius: 5000,
        options: {
          strokeColor: "#000",
          clickable: false,
        },
        toggle: 0
        };
      };
  for( let i = 180; i< 360 ; i++){
    outlets_sectionwise[i].section= 1
      outlets_sectionwise[i].circle = {
        radius: 5000,
        options: {
          strokeColor: "#000",
          clickable: false,
        },
        toggle: 0
        };
      };
  for( let i = 360; i< 540 ; i++){
    outlets_sectionwise[i].section= 2
      outlets_sectionwise[i].circle = {
        radius: 5000,
        options: {
          strokeColor: "#000",
          clickable: false
        },
        toggle: 0
        };
      };
  for( let i = 540; i< 720 ; i++){
    outlets_sectionwise[i].section= 3
      outlets_sectionwise[i].circle = {
        radius: 5000,
        options: {
          strokeColor: "#000",
          clickable: false
        },
        toggle: 0
        };
      };
  for( let i = 720; i< 900 ; i++){
    outlets_sectionwise[i].section= 4
      outlets_sectionwise[i].circle = {
        radius: 5000,
        options: {
          strokeColor: "#000",
          clickable: false
        },
        toggle: 0
        };
      };
  
  for( let i = 900; i< outlets_sectionwise.length ; i++){
    outlets_sectionwise[i].section= 5
      outlets_sectionwise[i].circle = {
        radius: 5000,
        options: {
          strokeColor: "#000",
          clickable: false
        },
        toggle: 0
        };
      };

  console.log(outlets)
  console.log("Outlets sectionwise",outlets_sectionwise)

      const map_options = {
        mapId: "b181cac70f27f5e6",
        disableDefaultUI: true,
        clickableIcons: true,
      }


     
export default class Map extends React.Component{    
    state = {circ:outlets,overlay:outlets_sectionwise,selected:select, center:{lat:9.955403,lng:76.3750268}};

    onOutletChange = (event) => {
      // event.preventDefault();
      this.props.selected.onOutletChange({value: this.state.selected});
  }
    
    componentDidUpdate(prevProps) {
      // Typical usage (don't forget to compare props):
      if (this.props.selected.selectedOut !== prevProps.selected.selectedOut) {
        const updated = this.state.circ.map(obj => {
          if (obj.id === this.props.selected.selectedOut && obj.id < 139) {
              return {...obj, circle:{
                radius: 8000,
                options: { strokeColor: "#ffff00" },
                clickable: false,
                visible: true
                } };
          }
          else if (obj.id < 139){
          return {...obj, circle:{
            radius: 8000,
            options: { strokeColor: "#ff0000"},
            clickable: false,
            visible: true
            } };}
          return obj;
        });

        this.setState({circ:updated, center:{lat: outlets[this.props.selected.selectedOut]['lat'],lng:outlets[this.props.selected.selectedOut]['lng']}})
      }
        if (this.props.selected.selectedSection !== prevProps.selected.selectedSection){
        const updated2 = this.state.overlay.map(obj => {
          if (obj.section === this.props.selected.selectedSection) {
              return {...obj, circle:{
                radius: 300,
                options: { strokeColor: "#000fff" ,
                clickable: false
                }
                } };
          }
          else if(obj.section !== this.props.selected.selectedSection){
          return {...obj, circle:{
            radius: 5000,
            options: { strokeColor: "#000",
            clickable: false,
           }
            } }
        }
        return obj;
      }
        );

        this.setState({overlay:updated2})
      }      

        
    }

    

    updateObjectInArray = (out , selection, outlet) => {
        
        const updated = this.state.circ.map(obj => {
          if (obj.id === out) {
            if(obj.arr.includes(outlet)){
            const index1 = obj.arr.indexOf(outlet);
            if (index1 > -1) { // only splice array when item is found
              obj.arr.splice(index1, 1); // 2nd parameter means remove one item only
            }}
            else {
              obj.arr.push(outlet)
            }
            if(obj.circle.toggle!==1){
              if(obj.arr.length>0){
              return {...obj, circle:{
                radius: 5000,
                options: { strokeColor: this.props.selected.selectedColor, fillColor:"#00ffbb"},
                toggle: 1
                } };}
              
              else{
                return {...obj, circle:{
                  radius: 5000,
                  options: { strokeColor: this.props.selected.selectedColor},
                  toggle: 1
                  } };
              }
            }
          
          else if(obj.circle.toggle===1){
            if(obj.arr.length>0){
          return {...obj, circle:{
            radius: 5000,
            options: { strokeColor: "#000fff",fillColor:"#00ffbb"},
            toggle: 0
            } };}
            else{
              return {...obj, circle:{
                radius: 5000,
                options: { strokeColor: "#000fff",fillColor:"#fff"},
                toggle: 0
                } };
            }
          }
        }
          return obj;
        });
        this.setState({circ: updated,selected:selection})
  
      };
    
      render(){
        
        if(this.props.selected.selectedOut === undefined){
          outlet_center = 0
        }
        else {
          outlet_center = this.props.selected.selectedOut 
        }

        
        
        console.log(this.props.selected.selectedSection)
        console.log(this.state.circ)
        console.log(this.state.selected)
        return(
        <GoogleMap
        zoom={8}
        center={this.state.center}
        mapContainerClassName="map-container"
        options={map_options}
      >
    {this.state.overlay.map(outlet_x => {
        return (
          <Fragment key={outlet_x.id}>
          {outlet_x.circle && <Circle center={{lat:outlet_x.lat, lng:outlet_x.lng}} radius={outlet_x.circle.radius} options={outlet_x.circle.options} /> } 
          </Fragment>
        )})}

        {this.state.circ.map(outlet => {
        
        return (
          <Fragment key={outlet.id}>{outlet.id < 139 && <Marker position={{lat:outlet.lat, lng:outlet.lng}} title={outlet.name}/>}
             {outlet.circle && <Circle center={{lat:outlet.lat, lng:outlet.lng}} radius={outlet.circle.radius} options={outlet.circle.options} 
             onClick= {(event) => {
              if(select[outlet_center].includes(outlet.id))
                  {const index = select[outlet_center].indexOf(outlet.id);
                    if (index > -1) { // only splice array when item is found
                      select[outlet_center].splice(index, 1); // 2nd parameter means remove one item only
                    }}
                    else {
                      select[outlet_center].push(outlet.id)
                    }
                    this.updateObjectInArray(outlet.id,select,outlet_center)
                    this.onOutletChange()
                    // console.log('selection from state',this.state.selected)
                  }} /> }          
            </Fragment>
         
        )})}
      </GoogleMap>);
      }
}