import React from "react";
import Select from 'react-select';

const sections = [
    {
        "value": 0,
        "label": "Section 1"
    },
    {
        "value": 1,
        "label": "Section 2"
    },
    {
        "value": 2,
        "label": "Section 3"
    },
    {
        "value": 3,
        "label": "Section 4"
    },
    {
        "value": 4,
        "label": "Section 5"
    },
    {
        "value": 5,
        "label": "Section 6"
    }
]

class SectionBar extends React.Component{
    state = { isOpen: false, value: 0, label: null, event: 0 };
    toggleOpen = () => {
        this.setState((state) => ({ isOpen: !state.isOpen }));
      };
    onSelectChange = (event) => {
        this.toggleOpen();
        this.setState({ value: event.value, label:event.label, event: event });
      };

    onFormSubmit = (event) => {
        event.preventDefault();
        this.props.onFormSubmit({value: this.state.value, label: this.state.label});
    }

    render(){
        
        const {event} = this.state;
        // console.log(value, label, event)
        return(<form className="ui segment" onSubmit={this.onFormSubmit}>
            <Select options={sections} onChange={this.onSelectChange} value={event} placeholder="Section">
            </Select>
            </form>);
    }
}

export default SectionBar;